/*========Google Fonts==========*/

@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;600;700&display=swap');

/*========Variables css========*/

:root{
  /*=======colors======*/
  /*color mode HSL(hue,saturation,lightness*/
  --first-color:#e3874f;
  --title-color:#fff;
  --text-color:#e3874f;
  --text2-color:#fff;
  --body-color:#000000;
  --nav-color:#0000;
  --container-color:#37363d;

  /*====Font and Typography====*/
  /*.5rem=8px | 1rem=16px*/
  --body-font:'Rubik', sans-serif;
  --h1-font-size:2.25rem;
  --h2-font-size:1.5rem;
  --h3-font-size:1.25rem;
  --normal-font-size:1rem;
  --small-font-size:0.875rem;
  --smaller-font-size:0.813rem;


  /*=======Font Weight======*/
  --font-medium:500;
  --font-semibold:600;
  --font-bold:700;


  /*=======Box shadow======*/
  --shadow:0px 5px 20px 0px rgb(69 67 96 /10%);

  /*=======Border Radius=====*/
  --border-radius:20px;
}
/*===responsive typography===*/
@media screen and(max-width:1024px) {
  :root{
    --h1-font-size:1.75rem;
  --h2-font-size:1.25rem;
  --h3-font-size:1rem;
  --normal-font-size:0.938rem;
  --small-font-size:0.813rem;
  --smaller-font-size:0.75rem;

  }
}
  /*=======Base======*/
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html{
  scroll-behavior: smooth;
}
body,
button,
input,
textarea{
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
}
body{
  background-color: var(--body-color);
  line-height: 1.7;
  color: var(--text-color);
}
h1,h2,h3{
  color: var(--title-color);
  font-weight: var(--font-semibold);
  line-height: 1.2;
}
ul{
  list-style: none;
}
a{
  text-decoration: none;
}
button{
  cursor: pointer;
  outline: none;
  border: none;
}
img{
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

/*=============Resuable css classes=======*/

.section{
  padding-top: 7rem;
  padding-bottom: 2rem;
}
.section-title{
  font-size: var(--h1-font-size);
  margin-left: 0.875rem;
  font-weight: var(--font-bold);
  position: relative;
  margin-bottom: 3.75rem;
  color: #fff;
}
.section-title::before{
  content: '';
  background: url(./assets/dots-bg.svg);
  height: 2.25rem;
  width: 2.25rem;
  position: absolute;
  left: -0.875rem;
  top: -0.875rem;
}
.btn{
  padding: 0.75rem 2rem;
  line-height: 1;
  border-radius: 1.875rem;
  -webkit-border-radius: 1.875rem;
  -moz-border-radius: 1.875rem;
  -ms-border-radius: 1.875rem;
  -o-border-radius: 1.875rem;
  box-shadow: 0 0 1px rgb(0 0 0 /0%); 
  border: 1px solid transparent;
  color:#fff;
  display: inline-block;
  background-color: var(--first-color);
  font-weight: var(--font-bold);
}
@keyframes button-push{
  50%{
    transform: scale(0.8);
    -webkit-transform: scale(0.8);
    -moz-transform: scale(0.8);
    -ms-transform: scale(0.8);
    -o-transform: scale(0.8);
}
100%{
  transform: scale(1);
  -webkit-transform: scale(1);
  -moz-transform: scale(1);
  -ms-transform: scale(1);
  -o-transform: scale(1);
}
}
.btn:hover{
  animation: button-push 0.3s linear 1;
  -webkit-animation: button-push 0.3s linear 1;
}